import React, { useEffect, useState } from 'react'
import 'react-loading-skeleton/dist/skeleton.css'
import "flatpickr/dist/themes/material_red.css";
import "react-datepicker/dist/react-datepicker.css";
import Flatpickr from "react-flatpickr";
import DatePicker from 'react-datepicker'
import { useForm } from "react-hook-form";
import { useParams, useLocation, useHistory } from 'react-router-dom';
import toast from 'react-hot-toast';
import config from "../config/toast";
import BackendService from "../services/BackendService";
import { Submit } from "../views/buttons"
import moment from 'moment';

export default function ScheduleSiteVisit() {
  const history = useHistory();
  const [startDate, setStartDate] = useState(new Date());
  const params = useParams()
  const [isLoading, setLoading] = useState(true);
  const [date, setDate] = useState('');
  const [time, setTime] = useState('');
  const [unitId, setUnitId] = useState(params.unitId);

  useEffect(() => {
    if (date && time && unitId)
      setLoading(false); 
    if (!(date && time && unitId))
      setLoading(true); 
  }, [unitId, date, time]);

  const doPost = (e) => {
    const data = {
      date: moment(date).format("DD-MM-YYYY"), time: moment(time).format("hh:mm:A"), unitId: parseInt(unitId)
    }
    e.preventDefault();
    BackendService
      .ScheduleSiteVisit(data)
      .then((response) => {
          if (response.status === 200 && response.data.statusCode === 200) {

            toast.success('You have been successfully scheduled inspection visit.', config);
            if (!params.Re) {
              history.push('/dashboard');
            } else {
              history.push('/rescheduleVisit');
            }
          } else {
            setLoading(false);
            toast.error('Application error.', config);
          }
      })
      .catch((error) => {
        // Error
        if (error.response) {
            // The request was made and the server responded with a status code
            // that falls out of the range of 2xx
            // console.log(error.response.data);
            // console.log(error.response.status);
            // console.log(error.response.headers);
            setLoading(false);
            toast.error("There is a problem while calling the api request.", { appearance: 'error', autoDismiss: true });
            //addToast("Incorrect username or password. Please try again.", { appearance: 'error', autoDismiss: true });
        } else if (error.request) {
            // The request was made but no response was received
            // `error.request` is an instance of XMLHttpRequest in the 
            // browser and an instance of
            // http.ClientRequest in node.js
            toast.error("Network problems happen when something disrupts the connection between your computer and the content you're trying to access.", { appearance: 'error', autoDismiss: true });
        } else {
            // Something happened in setting up the request that triggered an Error
            toast.error('Error', error.message);
        }
      });
    e.preventDefault();
  };

    return (
        <div className="container-fluid page-body-wrapper">
        <div className="main-panel">
          <div className="content-wrapper">
            <div className="row">

            <div className="col-md-6 grid-margin stretch-card">
        <div className="card">
          <div className="card-body">
            <h4 className="card-title">INSPECTION PROCESS</h4>
            <p className="card-description">
              Unit ID: #{ params.unitId }
            </p>
            <form className="forms-sample mb-10" onSubmit={doPost}>
              <div className="form-group">
                <label htmlFor="exampleInputUsername1"> {params.Re} Scheduled Date</label>
                <DatePicker dateFormat="dd-MM-yyyy" minDate={moment().toDate()} className="form-control bg-white" selected={date} onChange={(date) => setDate(date)} />
              </div>
              <div className="form-group">
              
                <label htmlFor="exampleInputEmail1">{params.Re}  Scheduled Time</label>
                <DatePicker 
                      className="form-control form-select bg-white"
                      selected={time}
                      onChange={(date) => setTime(date)}
                      showTimeSelect
                      showTimeSelectOnly
                      timeIntervals={15}
                      timeCaption="Time"
                      dateFormat="h:mm aa"
                />
              </div>
              <Submit 
                          loading={isLoading}
                          text='SUBMIT'
                      />
            </form>
          </div>
        </div>
      </div>
            </div>
            
          </div>
        </div>
        </div>
    )
}
